import {Component, OnChanges, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';

@Component({
   selector: 'app-map-point-preview',
   templateUrl: './map-point-preview.component.html',
   styleUrls: ['./map-point-preview.component.scss']
})
export class MapPointPreviewComponent implements OnInit, OnChanges {

   public pointId: number;

   constructor(
      private route: ActivatedRoute) {

   }

   ngOnInit() {
      console.log('MapPointPreviewComponent INIT');
      this.route.params
         .subscribe(params => {
            //this.pointId = params.pointId;
            this.testMethod(params.pointId);
            console.log('map-point-preview', params)
         })
   }

   ngOnChanges() {

   }

   testMethod(pointId) {
      this.pointId = pointId;
      console.log(222, pointId);
   }

   changePointId() {
      this.pointId = 111;
   }

}



